header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}
.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/*======================= CTA =================*/
.cta {
  display: flex;
  margin-top: 4rem;
  gap: 1.2rem;
  justify-content: center;
}

/*======================= Social =================*/
.test {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
  z-index: 10;
}
.test::after {
  content: "";
  width: 1px;
  height: 2rem;
  background-color: var(--color-primary);
  position: absolute;
  top: 100%;
  left: calc(50% - 0.5px);
  margin-top: 0.4rem;
}

/*======================= ME =================*/
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 28rem;
  height: 34rem;
  position: absolute;
  left: calc(50% - 14rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/*======================= Scroll Down =================*/

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/*================== Media Queries (md) ===========*/

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/*================== Media Queries (sm) ===========*/

@media screen and (max-width: 600px) {
  header {
    height: 120vh;
  }

  .scroll__down,
  .test {
    display: none;
  }
}
